<template>
  <a-layout id="components-layout-demo-fixed">
    <a-layout-header :style="{ position: 'fixed', zIndex: 1, width: '100%', zIndex: '1000'}">
      <div class="logo">
        <a-icon type="thunderbolt" theme="filled" style="font-size: 40px; color: white; opacity: 0.3"/>
        <p id="header">快 链</p>
      </div>
      <a-menu
              theme="dark"
              mode="horizontal"
              :default-selected-keys="['1']"
              :style="{ lineHeight: '64px' }"
      >
        <a-menu-item key="1">
          <a href="/manage">主页</a>
        </a-menu-item>
        <a-menu-item key="2">
          <a href="/">项目</a>
        </a-menu-item>
        <a-menu-item key="3">
          <a-button type="white" icon="key" ghost @click="showModal">{{tokenWord}}
          </a-button>
        </a-menu-item>
      </a-menu>
    </a-layout-header>
    <a-layout-content :style="{ padding: '0 50px', paddingTop: '100px',
    backgroundColor: 'white'
    }">
      <div style="background-color: white;">
        <a-row>
          <a-col :span="16" :xs="24" :sm="24" :md="16" :lg="16" :xl="14" :xxl="10">
            <a-page-header
                    style="margin-left: -19px"
                    title="添加短链接"
                    sub-title="输入原始网址和关键字以添加短链接"
            />
            <a-input placeholder="原始的链接地址，可不加 http(s)" size="large" v-model="addFrom" >
              <a-icon slot="prefix" type="global" />
            </a-input>
            <a-input
                    style="margin-top: 15px"
                    size="large"
                    addon-before="http://go.mazhangjing.com/" placeholder="简短容易记忆的单词"
            v-model="addKey">
            </a-input>
            <a-button style="margin-top: 15px" type="primary" size="large" block
            @click="addToDatabase">
              添加到数据库
            </a-button>
            <a-divider dashed/>
          </a-col>
        </a-row>
        <a-row>
          <a-col :span="16"  :xs="24" :sm="24" :md="16" :lg="16" :xl="14" :xxl="10">
            <a-page-header
                    style="margin-left: -19px"
                    title="查询短链接"
                    sub-title="输入需要查询的关键字以查询短链接"
            />
            <a-input
                    size="large"
                    addon-before="输入需要查询的关键字" placeholder="like home"
                    v-model="checkFrom"
            >
            </a-input>
            <a-input style="margin-top: 15px"
                     placeholder="未匹配到结果" size="large"
                     v-model="checkResult"
            >
              <a-icon slot="prefix" type="global" />
            </a-input>
            <a-button style="margin-top: 15px" type="primary" size="large" block ghost
                      @click="checkDatabase">
              查询数据库
            </a-button>
            <a-divider dashed/>
          </a-col>
        </a-row>
        <a-row>
          <a-col :span="16"  :xs="24" :sm="24" :md="16" :lg="16" :xl="14" :xxl="10">
            <a-page-header
                    style="margin-left: -19px"
                    title="删除短链接"
                    sub-title="输入需要删除的关键字以删除短链接"
            />
            <a-input
                    style="margin-top: 15px"
                    size="large"
                    addon-before="http://go.mazhangjing.com/" placeholder="需删除的关键字"
                    v-model="deleteKey">
            </a-input>
            <a-button style="margin-top: 15px" type="danger" size="large" block ghost
            @click="deleteEntity">
              从数据库删除
            </a-button>
          </a-col>
        </a-row>
        <a-modal
                title="登录服务器"
                :visible="visible"
                :confirm-loading="confirmLoading"
                okText = "获取 Token"
                cancelText="取消登录"
                @ok="handleOk"
                @cancel="handleCancel">
          <a-input placeholder="用户名" size="default" v-model="userName" >
            <a-icon slot="prefix" type="user" />
          </a-input>
          <a-input-password
                  style="margin-top: 7px"
                  size="default"
                  placeholder="密码"
                  v-model="passWord"
          >
            <a-icon slot="prefix" type="key" />
          </a-input-password>
        </a-modal>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
      </div>
    </a-layout-content>
    <a-layout-footer :style="{ textAlign: 'center'
    }">
      ©️ Marvin Studio 2016-2020 | Create by <a id="name" href="http://www.mazhangjing.com">Corkine Ma</a> |<a-button type="link" @click="showVersion">v{{version}} 更新记录</a-button>
    </a-layout-footer>
  </a-layout>
</template>

<style>
  #components-layout-demo-fixed .logo {
    width: 120px;
    height: 31px;
    background: rgba(255, 255, 255, 0.2);
    margin: 16px 24px 16px 0;
    float: left;
  }
  #header {
    font-size: 23px;
    color: white;
    line-height: 0;
    margin-top: -48px;
    margin-left: 47px;
  }
  html {
    background-color: #ebeef1;
  }
  #name {
    color: #606162;
  }
</style>

<script>
  import {notification} from 'ant-design-vue';
  import axios from 'axios';
  export default {
  name: 'App',
  components: {
  },
  mounted() {
    //从本地存储提取 token，如果找不到，则跳过，反之则验证，
    //通过验证后设置为通过加载的 token，过期后清空此 token 存储。
    const token = localStorage.getItem("token");
    if (token !== null) {
      axios.get(this.tokenURL,{
        params: {
          token: token
        }
      }).then(() => {
        this.isSavedToken = true;
        this.token = token;
      }).catch(() => {
        this.token = "";
        localStorage.removeItem("token");
      })
    }
  },
  data() {
    return {
      version: "1.1.0",
      logs: {
        "1.0.0":"2020年08月24日，提供了查询、添加和删除短链接的 API",
        "1.1.0":"2020年08月24日，提供了 Token 的本地存储和自动加载和验证机制"
      },
      basicURL: "",
      tokenURL: "/token",
      addURL: "/add",
      checkURL: "/check/",
      deleteURL: "/deleteKey/",
      userName: "",
      passWord: "",
      visible: false,
      confirmLoading: false,
      token: "",
      isSavedToken: false,
      addFrom: "",
      addKey: "",
      checkFrom: "",
      checkResult: "",
      deleteKey: ""
    };
  },
  computed: {
    tokenWord() {
      //如果没有 Token，则获取，反之如果加载，则判断是从存储加载还是重新获取的
      if (this.token === "") return "获取 Token";
      else {
        if (this.isSavedToken) return "已验证 Token";
        return "已获取 Token";
      }
    }
  },
  methods: {
    showVersion() {
      notification.info({
        message: "当前版本 " + this.version,
        description: this.logs[this.version],
        duration: 10
      })
    },
    showModal() {
      this.visible = true;
      this.userName = "";
      this.passWord = "";
    },
    handleOk() {
      console.log(this.userName, this.passWord);
      this.confirmLoading = true;
      if (this.userName && this.passWord) {
        axios.get(this.tokenURL, {
          params: {
            user: this.userName,
            password: this.passWord
          }
        }).then((res) => {
          console.log(res);
          if (res.status === 200 && res.data.status === 1) {
            notification.success({
              message: "成功获取 Token",
              description: "Token 已保存，可直接使用服务。",
              duration: 5
            })
          }
          this.saveToken(res.data.token);
        }).catch((res) => {
          console.log(res);
          notification.warn({
            message: "Token 获取失败",
            description: "用户名和密码校验错误，请重试",
            duration: 5
          })
        });
      } else {
        notification.warn({
          message: "请输入用户名和密码以完成鉴权"
        })
      }
      this.confirmLoading = false;
      this.visible = false;
    },
    handleCancel() {
      this.visible = false;
    },
    checkToken() {
      return this.token !== "";
    },
    saveToken(token) {
      //将新 Token 保存到变量并设置重新获取的，同时保存到本地存储
      this.token = token;
      this.isSavedToken = false;
      localStorage.setItem("token",token);
    },
    addToDatabase() {
      console.log("Add Entity: ",this.addFrom, this.addKey);
      if (this.checkToken() && this.addFrom && this.addKey) {
        axios.get(this.addURL,{
          params: {
            keyword: this.addKey,
            redirectURL: this.addFrom,
            note: "Add by 快链 in AJAX - VUE",
            token: this.token
          }
        }).then((res) => {
          console.log(res);
          if (res.status === 200) {
            if (res.data.status === 0) {
              notification.warn({
                message: "插入数据失败",
                description: res.data.message
              })
            } else if (res.data.status === 1) {
              notification.success({
                message: "插入数据成功",
                description: res.data.message
              })
            }
          }
        }).catch((res) => {
          console.log("ERROR",res);
          notification.warn({
            message: "插入时发生预期之外的错误",
            description: "详情请参见控制台"
          })
        })
      } else {
        notification.warn({
          message: "请获取 Token、并提供希望插入的链接以及其关键词再试"
        })
      }
    },
    checkDatabase() {
      console.log("Checking ", this.checkFrom);
      if (this.checkToken() && this.checkFrom) {
        axios.get(this.checkURL + this.checkFrom, {
          params: {
            token: this.token
          }
        }).then((res) => {
          console.log("Check Res", res);
          if (res.status === 200) {
            if (res.data.status === 1) {
              this.checkResult = res.data.entity.redirectURL;
            } else {
              this.checkResult = "";
              notification.info({
                message: "未找到匹配记录。"
              })
            }
          }
        }).catch((res) => {
          console.log("Check Error", res);
          notification.warn({
            message: "出现未预期的错误，请前往控制台查看"
          })
        })
      } else {
        notification.warn({
          message: "请获取 Token，并且提供需要查询的关键字"
        })
      }
    },
    deleteEntity() {
      if (this.checkToken() && this.deleteKey) {
        axios.get(this.deleteURL + this.deleteKey, {
          params: {
            token : this.token
          }
        }).then((res) => {
          console.log("DELETE RES",res);
          if (res.status === 200) {
            if (res.data.status === 0) {
              notification.info({
                message: "未在数据库找到此条数据，可能已经删除。"
              })
            } else if (res.data.status === 1) {
              notification.success({
                message: "已经成功将记录从数据库删除。"
              })
            }
          }
        }).catch((res) => {
          console.log("DELETE ERR", res);
          notification.warn({
            message: "发生了未预期的错误，参见控制台"
          })
        })
      } else {
        notification.warn({
          message: "请获取 Token、输入需要删除的短链接以继续操作。",
          description: "删除记录需要 Admin 权限，请确保 Token 具备此权限"
        })
      }
    }
  },
}
</script>

