import Vue from 'vue'
import App from './App.vue'
import 'ant-design-vue/dist/antd.css';

import { Button, Layout, Menu, Breadcrumb, Icon, Input, PageHeader, Row, Col, Divider, Modal, notification }
from 'ant-design-vue';

Vue.config.productionTip = false;

Vue.use(Button);
Vue.use(Layout);
Vue.use(Menu);
Vue.use(Breadcrumb);
Vue.use(Icon);
Vue.use(Input);
Vue.use(PageHeader);
Vue.use(Row);
Vue.use(Col);
Vue.use(Divider);
Vue.use(Modal);
notification.config({
  top: '77px',
  placement: 'topRight',
  duration: 5.5
});
new Vue({
  render: h => h(App),
}).$mount('#app');
